
.long-comment .box {
  width: 100%;
  height: 40px;
}
.long-comment .long-comment-number {
  font-size: 16px;
  color: #333;
  line-height: 1.5em;
  font-weight: 700;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  margin: 0;
  padding: 9px 20px;
}
.long-comment .comment-list {
  padding: 0;
}
.long-comment .comment-list .icon-shafa {
    position: absolute;
    left: 50%;
    top: 40%;
    margin-left: -90px;
    margin-top: -90px;
    font-size: 180px;
    color: #e4e4e4;
    display: none;
}
.long-comment .comment-list .comment-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 15px;
    color: #666;
    border-bottom: 1px solid #eee;
    padding: 20px;
}
.long-comment .comment-list .comment-item .avatar {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 40px;
              flex: 0 0 40px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
}
.long-comment .comment-list .comment-item .comment-content {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding-left: 10px;
}
.long-comment .comment-list .comment-item .comment-content .author {
        font-size: 19px;
        color: #000;
        font-weight: 400;
}
.long-comment .comment-list .comment-item .comment-content .icon-dianzan {
        float: right;
        color: #b4b4b4;
}
.long-comment .comment-list .comment-item .comment-content .text {
        margin: 10px 0 !important;
        line-height: 18px;
}
.long-comment .comment-list .comment-item .comment-content .reply {
        margin: 10px 0 !important;
        color: #929292;
        line-height: 18px;
}
.long-comment .comment-list .comment-item .comment-content .reply .reply-author {
          color: #000;
}
.long-comment .comment-list .comment-item .comment-content .hideReply {
        overflow: none;
}
.long-comment .comment-list .comment-item .comment-content .date {
        color: #ccc;
}
.long-comment .comment-list .comment-item .comment-content .expand {
        display: inline-block;
        float: right;
        width: 45px;
        height: 20px;
        text-align: center;
        line-height: 20px;
        background: #d8e4f2;
}



