
.news-menu {
  position: fixed;
  width: 100%;
  height: 44px;
  left: 0;
  bottom: 0;
}
.news-menu .menu-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    padding: 0;
    text-align: center;
    line-height: 44px;
    border-top: 1px solid #f5f5f5;
    background: #fff;
}
.news-menu .menu-wrap .menu-item {
      position: relative;
      width: 20%;
      color: #999;
}
.news-menu .menu-wrap .menu-item i {
        font-size: 24px;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
}
